@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

body {
    font-family: "VT323", monospace;
    width: 100%;
    height: 500px; /* Adjust height as needed */
    background-image: url('https://www.wallpaperhub.app/_next/image?url=https%3A%2F%2Fcdn.wallpaperhub.app%2Fcloudcache%2Fb%2Fd%2F7%2F6%2F4%2Fb%2Fbd764bb25d49a05105060185774ba14cd2c846f7.jpg&w=4500&q=100');
    background-size: cover; /* Cover the entire element */
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
}

.main-container{
    height: 600px;
    width: 1200px;
    background-color: #c7bc9d;
    position: absolute;
    top: 10%;
    left: 10%;
}

.main-head{
    position: relative;
    top: 5px;
    display: flex;
    left: 5px;
    width: 1190px;
    height: 30px;
    color: white;
    background-color: rgb(16, 16, 160);
}

.minimize-icon,.maximize-icon,.cancel-icon{
    position: relative;
    top: 2px;
    left: 738px;
    height: 27px;
    width: 27px;
    background-color: #c7bc9d;
    border-right: 3px solid rgb(100, 100, 100);
    border-bottom: 3px solid rgb(100, 100, 100);
    border-left: 2px solid #ddd3b8;
    border-top: 2px solid #ddd3b8;
    margin-right: 5px;
}

.minimize,.maximize,.cancel{
    position: absolute;
    top: -10px;
    left: 5px;
    color: black;
    font-size: 30px;
}

.subhead{
    position: relative;
    top: 8px;
    display: flex;
    left: 5px;
    width: 1190px;
    height: 30px;
    color: white;
    background-color: transparent;
}

.button{
    font-size: 25px;
    border: 0px;
    background-color: transparent;
